

import { defineComponent, ref, reactive, computed, Ref, onMounted, toRefs, onUnmounted } from 'vue'

import ActionModalVue from '../../../components/list/ActionModal.vue';
import PageHeader from '../../../components/list/PageHeader.vue';
import PackageService from "../../../services/package";
import { PackageCreateRequest } from '../../../types/package';
import LocationService from "../../../services/location";
import { LocationListRequest } from '../../../types/location';
import ImageService from "../../../services/imageUpload";
import { ListImageRequest } from "../../../types/imageupload";
import { SaveImageRequest } from '../../../types/imageupload';
import { useVuelidate } from '@vuelidate/core'
import { email, helpers, integer, maxLength, minLength, numeric, required, url } from '@vuelidate/validators'
import { useRouter } from 'vue-router';
import { useI18n } from 'vue-i18n';
import Loader from '../../../components/comp/Loader.vue';
import config from '../../../../../kaka-web/src/config';
import axios from 'axios';


let createpackage: PackageCreateRequest= {
name: "",
source: "",
destination: "",
description: "",
duration: "",
days: "",
nights: "",
persons: "",
base_price: "",
discount_price: "",
price: "",
booking_amount: "",
offer_applicable: "",
icon: "",
banner: "",
thing_will_love: "",
}
let listFilterData : LocationListRequest={
        perPage:100000,
        page:1,
}
let saveimage : SaveImageRequest={
        name:'',
        url:'',
}

export default defineComponent({
    components: {ActionModalVue, PageHeader, Loader },
    name: 'car-create',
    setup(){
        const { t } = useI18n();
        const router = useRouter();
        const YOUR_GOOGLE_MAPS_API_KEY = config.googleApiKey;
        const center = ref({ lat: 37.7749, lng: -122.4194 });
         const zoom = ref(10);
        const fileInput = ref<HTMLInputElement | null>(null);
        const selectedFile = ref<File | null>(null);
        const state = reactive(
            {
                isLoading:true,
                form:createpackage,
                 filterForm:listFilterData,
                tableData: <any>[],
                locationData: <any>[],
                backendErrors:<any>{},
                phoneValidate:false,
                authModalOpen:false,
                imageModel: false,
                bannerModel: false,
                addImage:false,
                saveimageform: saveimage,
                 iform: {
                     file: null as File | null,
                },

            }
        );

        const createRule = {
            form:{
                name: {
                        required1: helpers.withMessage(`${t('message.required')}`, required),
                    },
                source: {
                        required1: helpers.withMessage(`${t('message.required')}`, required),
                        numeric: helpers.withMessage(t('message.number-is-required'), numeric),
                    },
                destination: {
                        required1: helpers.withMessage(`${t('message.required')}`, required),
                        numeric: helpers.withMessage(t('message.number-is-required'), numeric),
                    },
                description: {
                        required1: helpers.withMessage(`${t('message.required')}`, required),
                    },
                duration: {
                        required1: helpers.withMessage(`${t('message.required')}`, required),
                    },
                days: {
                        required1: helpers.withMessage(`${t('message.required')}`, required),
                        numeric: helpers.withMessage(t('message.number-is-required'), numeric),
                    },
                nights: {
                        required1: helpers.withMessage(`${t('message.required')}`, required),
                        numeric: helpers.withMessage(t('message.number-is-required'), numeric),
                    },
                persons: {
                        required1: helpers.withMessage(`${t('message.required')}`, required),
                        numeric: helpers.withMessage(t('message.number-is-required'), numeric),
                    },
                base_price: {
                        required1: helpers.withMessage(`${t('message.required')}`, required),
                        numeric: helpers.withMessage(t('message.number-is-required'), numeric),
                    },
                discount_price: {
                        required1: helpers.withMessage(`${t('message.required')}`, required),
                        numeric: helpers.withMessage(t('message.number-is-required'), numeric),
                    },
                price: {
                        required1: helpers.withMessage(`${t('message.required')}`, required),
                        numeric: helpers.withMessage(t('message.number-is-required'), numeric),
                    },
                booking_amount: {
                        required1: helpers.withMessage(`${t('message.required')}`, required),
                        numeric: helpers.withMessage(t('message.number-is-required'), numeric),
                    },
                offer_applicable: {
                        required1: helpers.withMessage(`${t('message.required')}`, required),
                    },
                icon: {
                        required1: helpers.withMessage(`${t('message.required')}`, required),
                        url: helpers.withMessage(t('message.Valid-url'), url)
                    },
                banner: {
                        required1: helpers.withMessage(`${t('message.required')}`, required),
                        url: helpers.withMessage(t('message.Valid-url'), url)

                    },
                
            },
        }
         const V1$ = useVuelidate(createRule, state)
        const getBackendError=(item:any)=>{
            for (const key in state.backendErrors) {
                if(item==key){
                    return state.backendErrors[key];
                }
            }
        }
         const createRule2 = {
            saveimageform:{
                name: {
                        required1: helpers.withMessage(`${t('message.required')}`, required),
                        },
            },
        }
         const V2$ = useVuelidate(createRule2, state)
        const getBackendError2=(item:any)=>{
            for (const key in state.backendErrors) {
                if(item==key){
                    return state.backendErrors[key];
                }
            }
        }
        

        const packagesSubmit = () => {
            V1$?.value.$touch();
            if (V1$?.value.$invalid) {
                return;
            }else{
        new PackageService().CreatePackage(state.form).then((response:any)=>{
                if(response !== undefined && response.data !== undefined){
                    router.push({ path:"/admin/packages" })
                }
            }).catch((error)=>{
                console.log("error",error)
            });
    }
};

// file upload start
 const file = ref<File | null>(null);
    const fileSize = ref<number | null>(null);

    const onFileChange = (event: any) => {
      const selectedFile = event.target.files[0];
      file.value = selectedFile;
      fileSize.value = selectedFile.size;
      state.iform.file = selectedFile;
      const parts = selectedFile.name.split('.');
      const parts2 = parts[0];
      state.saveimageform.name = parts2
      submitImage()
    };
     const submitImage = () => {
    const formData = new FormData();
    if (state.iform.file) {
      formData.append("image", state.iform.file); 
      const url = config.authUrl + "/image/upload";
      const token = localStorage.getItem('token');
      axios.post(url, formData, {
        headers: {
          'Authorization':'Bearer '+token,
          "Content-Type": "multipart/form-data",
          "X-API-KEY": config.apiKey
        }
      })
      .then((response: any) => {
        console.log("Upload successful! 1", response.data);
        state.saveimageform.url = response.data.data
        
      })
      .catch((error: any) => {
        console.error("Error:", error);
      });
    }
};
const submitForm = () => {
    V2$?.value.$touch();
     if (V2$?.value.$invalid) {
         return;
     }else{
          const url = config.authUrl + "/image";
            const token = localStorage.getItem('token');
            const params = {
                name: state.saveimageform.name,
                url: state.saveimageform.url,
              };
            axios.post(url, params, {
              headers: {
                'Authorization':'Bearer '+token,
                'Content-Type': 'application/json',
                "X-API-KEY": config.apiKey
              }
            })
            .then((response: any) => {
                ImageList();
                state.saveimageform.name = '';
              state.addImage = false;

            })
            .catch((error: any) => {
              console.error("Error:", error);
        });
    }
};




function selectImage(){
    state.imageModel = true;
}
function closeIcon(){
    state.imageModel = false;
}
function selectBanner(){
    state.bannerModel = true;
}
function closeBanner(){
    state.bannerModel = false;
}
function iconUrl(url:any){
    state.form.icon = url
    state.imageModel = false;
}
function bannerUrl(url:any){
    state.form.banner = url
    state.bannerModel = false;
}
function addMore(){
    state.addImage = true;
}
function closeAddImage(){
    state.addImage = false;
}
const ImageList = ()=>{
    new ImageService().listOfImage().then((response:any)=>{
      console.log("response",response)
        if(response !== undefined){
             state.tableData = response.data.data;
            
        }
    }).catch((error)=>{});
}

 const ListLocations = ()=>{
            new LocationService().listOfLocation(state.filterForm).then((response:any)=>{
                console.log("loc response",response)
                if(response !== undefined){
                    state.locationData = response.data.data
                }
            }).catch((error)=>{});
        }

        const reset=()=>{
            state.form.name = "",
            state.form.source = "",
            state.form.destination = "",
            state.form.description = "",
            state.form.duration = "",
            state.form.days = "",
            state.form.nights = "",
            state.form.persons = "",
            state.form.base_price = "",
            state.form.discount_price = "",
            state.form.price = "",
            state.form.booking_amount = "",
            state.form.offer_applicable = "",
            state.form.icon = "",
            state.form.banner = "",
            state.form.thing_will_love = "",
            V1$.value.$reset();
        }
        
       
        onMounted(()=>{
            ListLocations();
            ImageList();
            setTimeout(() => {
              state.isLoading = false  
            }, 1000);
        })
        onUnmounted(() => {
            reset();
        });


        return {
            ...toRefs(state),getBackendError, V1$, packagesSubmit, fileInput,reset,
             center,zoom,selectImage,ImageList,iconUrl,selectBanner,bannerUrl,ListLocations,
             closeIcon, closeBanner,addMore,closeAddImage,submitForm,V2$,getBackendError2,onFileChange
        }
    }
  
})
